  border-bottom: 2px solid transparent;
  color: #f14214;
  text-decoration: none;
}
.main-map__title-secondary--accent:hover {
  color: #f14214;
}
.main-map__title-secondary--add {
  white-space: nowrap;
}
@media screen and (max-width: 450px) {
  .main-map__title-secondary--add {
    white-space: unset;
  }
}

.main-section__portfolio-index {
  border-top: 3px solid #f14214;
}

.portfolio-index {
  position: relative;
  margin: auto;
  max-width: 1920px;
  height: 165px;
  overflow: hidden;
}

.portfolio-index__in {
  position: absolute;
  top: 0;
  left: 50%;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  transform: translateX(-50%);
}

.portfolio-index__item {
  margin-left: -1px;
  width: 165px;
  height: 165px;
  transform: skewX(-60deg);
  overflow: hidden;
}

.portfolio-index__item_in {
  position: relative;
  left: -150px;
  display: block;
  width: 500px;
  height: 165px;
  background-position: 0 165px;
  transform: skewX(60deg);
}
.portfolio-index__item_in:hover {
  background-position: 0 0;
}
.portfolio-index__item_in--1 {
  background-image: url("/images/main/portfolio/1.jpg");
}
.portfolio-index__item_in--2 {
  background-image: url("/images/main/portfolio/2.jpg");
}
.portfolio-index__item_in--3 {
  background-image: url("/images/main/portfolio/3.jpg");
}
.portfolio-index__item_in--4 {
  background-image: url("/images/main/portfolio/4.jpg");
}
.portfolio-index__item_in--5 {
  background-image: url("/images/main/portfolio/5.jpg");
}
.portfolio-index__item_in--6 {
  background-image: url("/images/main/portfolio/6.jpg");
}
.portfolio-index__item_in--7 {
  background-image: url("/images/main/portfolio/7.jpg");
}
.portfolio-index__item_in--8 {
  background-image: url("/images/main/portfolio/8.jpg");
}
.portfolio-index__item_in--9 {
  background-image: url("/images/main/portfolio/9.jpg");
}
.portfolio-index__item_in--10 {
  background-image: url("/images/main/portfolio/10.jpg");
}
.portfolio-index__item_in--11 {
  background-image: url("/images/main/portfolio/11.jpg");
}
.portfolio-index__item_in--12 {
  background-image: url("/images/main/portfolio/12.jpg");
}

.portfolio-index__link {
  padding: 20px;
  text-align: center;
  font-size: 11px;
}
.portfolio-index__link a {
  color: #f14214;
}

.index-submap {
  width: 100%;
  height: 96px;
  background: url("/images/main/main_map_under.png") no-repeat 50% 0;
}

.index-submap--nobg {
  background: none;
}

.arrow-bar {
  position: relative;
}

.arrow-bar__arrow {
  width: 48px;
  height: 48px;
  position: absolute;
  left: 50%;
  margin-left: -23px;
  top: -23px;
  background: url("/images/main/circle_down_sprite.png") no-repeat 0 0;
  cursor: pointer;
}

.arrow-bar__arrow:hover {
  cursor: pointer;
}

.index-submap__arrow--up {
  background: url("/images/main/circle_up_sprite.png") no-repeat 0 0;
}

.more-link {
  font-size: 12px;
  color: #999999;
}

.arrow-bar {
  border-bottom: 1px solid #cacaca;
}

.super-hr {
  height: 9px;
  border: none;
  background: url("/images/main/super_hr_bg.png");
  margin: 50px 0;
}

.small-hr {
  height: 4px;
  border: none;
  background: url("/images/main/super_hr_bg.png");
  margin: 10px 0;
}

.sixbox {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 65px;
  margin-top: 34px;
  margin-bottom: 130px;
  overflow: hidden;
}
@media screen and (max-width: 1000px) {
  .sixbox {
    column-gap: 40px;
  }
}
@media screen and (max-width: 900px) {
  .sixbox {
    column-gap: 20px;
  }
  .sixbox .sixbox-wrapper {
    transform: scale(0.9);
  }
}
@media screen and (max-width: 800px) {
  .sixbox .sixbox-wrapper:nth-child(3) {
    display: none;
  }
}
@media screen and (max-width: 600px) {
  .sixbox {
    column-gap: 0px;
  }
  .sixbox .sixbox-wrapper {
    transform: scale(0.8);
  }
}
@media screen and (max-width: 500px) {
  .sixbox {
    flex-direction: column-reverse;
    margin-top: -34px;
    margin-bottom: 50px;
  }
}

.sixbox-wrapper {
  border: 0px dashed gray;
  position: relative;
  overflow: visible;
  width: 196px;
  height: 253px;
  flex-shrink: 0;
}

.sixbox__icon {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  color: #fff;
  pointer-events: none;
}
.sixbox__icon--services {
  left: 50%;
  top: 50%;
  transform: translate(-50%, calc(-50% - 40px));
}
.sixbox__icon--faq {
  left: 50%;
  top: 50%;
  transform: translate(-50%, calc(-50% - 40px));
}

.sixbox__block {
  position: absolute;
}
.sixbox__block--services {
  top: -50px;
  left: -111px;
  width: 363px;
  height: 330px;
  background: url("/images/main/sixbox/sixbox_question.png") no-repeat 10px 20px;
}
.sixbox__block--services .sixbox__text {
  top: 50px;
  left: 110px;
}
.sixbox__block--form {
  top: -45px;
  left: -11px;
  width: 213px;
  height: 330px;
  background: url("/images/main/sixbox/sixbox_form.png") no-repeat;
}
.sixbox__block--form:hover {
  background-position: 100% 100%;
}
.sixbox__block--form .sixbox__text {
  top: 50px;
  left: 10px;
  color: #333333;
}
.sixbox__block--faq {
  top: -45px;
  left: -60px;
  width: 361px;
  height: 330px;
  background: url("/images/main/sixbox/sixbox_blog.png") no-repeat 10px -10px;
}
.sixbox__block--faq .sixbox__text {
  top: 50px;
  left: 58px;
}

.sixbox__text,
a.sixbox__text {
  position: absolute;
  color: #ffffff;
  font-size: 26px;
  font-family: "robofanregular";
  text-align: center;
  width: 198px;
  display: block;
  text-decoration: none;
  height: 70px;
  padding-top: 130px;
}
.sixbox__text span,
a.sixbox__text span {
  font-size: 14px;
  display: block;
}